.card-container {
  display: flex;
  flex-direction: column;
  background-color: #95dada;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 25px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
}

.card-container:hover {
  transform: scale(1.05);
}

.card-img {
  display: block;
  width: 200px;
  height: 200px;
  margin-left: auto;
  margin-right: auto
}
